<template>
  <div class="home">
    <Form v-model="formFilter" :label-width="100">
      <Row>
       <!-- <Col span="4">
          <FormItem label="任务id">
            <Input v-model="formFilter.id" clearable @on-change="getData(1)"></Input>
          </FormItem>
        </Col> -->
        <Col span="4">
          <FormItem label="任务名称">
            <Input v-model="formFilter.name" clearable @on-change="getData(1)"></Input>
          </FormItem>
        </Col>
        <!-- <Col span="4">
          <FormItem label="任务状态">
            <Input v-model="formFilter.jobStatus" clearable @on-change="getData(1)"></Input>
          </FormItem>
        </Col> -->
      </Row>
    </Form>
    <div class="spaceBetween bottomSpace">
      <div>
        <Button shape="circle" @click="deleteConfirm" class="btnForm" :type="isSelected?'error':'default'">删除选中项</Button>
      </div>
      <div>
        <Button shape="circle" class="btnForm" type="primary" @click="showInsert">新增</Button>
      </div>
    </div>
    <Table ref="table" :height="tableHeight" border stripe :columns="columns" :data="tableData" @on-row-dblclick="showUpdate" @on-selection-change="e=>isSelected = e.length>0">
      <template slot-scope='{row,index}' slot="jobStatus" >
        <i-switch v-model="row.jobStatus" :true-value="1" @on-change="changeJobStatus(row)" :false-value="0"></i-switch>
      </template>
    </Table>
    <!-- <div class="page topSpace">
      <Page :total="total" show-total show-elevator show-sizer transfer :current="formFilter.pageNum" :page-size="formFilter.pageSize" :page-size-opts="[10,20,30]"></Page>
    </div> -->
    
    <Modal v-model="modal" width="1000" :title="modalTitle">
      <Form :model="modalForm" :label-width="100" ref="modalForm" :rules="modalValidate">
        <Row>
          <Col span="12">
            <FormItem label="任务名称" prop="jobName">
              <Input v-model="modalForm.jobName" clearable></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="任务描述" prop="jobDesc">
              <Input v-model="modalForm.jobDesc" clearable></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="cron表达式" prop="cronExpression">
              <Input v-model="modalForm.cronExpression" clearable></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="执行接口" prop="targeturl">
              <Input v-model="modalForm.targeturl" clearable></Input>
            </FormItem>
          </Col>
        </Row>
      </Form>
      
      <div slot="footer">
        <Button type="primary" shape="circle" @click="submit">提交</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  export default{
    name:"Home",
    data(){
      return{
        total:0,
        isSelected:false,
        
        formFilter:{
          pageSize:20,
          pageNum:1
        },
        columns:[
          {
            type:"selection",
            key:"select",
            width:60,
            align:"center"
          },
          {
            title:"序号",
            key:"index",
            minWidth:80,
            align:"center",
            render:(h,params)=>{
              return h('div',this.formFilter.pageSize*(this.formFilter.pageNum-1)+1+params.index)
            }
          },
          {
            title:"任务id",
            key:"id",
            align:"center",
            minWidth:100,
          },
          {
            title:"任务名称",
            key:"jobName",
            align:"center",
            minWidth:100,
          },
          {
            title:"任务描述",
            key:"jobDesc",
            align:"center",
            minWidth:100,
          },
          {
            title:"cron表达式",
            key:"cronExpression",
            align:"center",
            minWidth:180,
          },
          {
            title:"执行接口",
            key:"targeturl",
            align:"center",
            minWidth:200,
          },
          {
            title:"任务状态",
            key:"jobStatus",
            align:"center",
            minWidth:100,
            slot:"jobStatus"
          },
          {
            title:"创建时间",
            key:"createTime",
            align:"center",
            minWidth:180,
          },
          {
            title:"更新时间",
            key:"updateTime",
            align:"center",
            minWidth:180,
          },
          {
            title:"执行",
            align:"center",
            minWidth:150,
            render:(h,params)=>{
              return h('Button',{
                props:{
                  type:"primary",
                  disabled:params.row.jobStatus==0
                },
                on:{
                  "click":e=>{
                    
                  }
                }
              },"运行一次")
            }
          }
        ],
        tableData:[],
        tableHeight:400,
        
        modalTitle:"新增定时任务",
        modal:false,
        modalForm:{},
        modalValidate:{
          jobName:[
            {required:true,message:"任务名称不可为空",trigger:"change"}
          ],
          cronExpression:[
            {required:true,message:"cron表达式不可为空",trigger:"change"}
          ],
          targeturl:[
            {required:true,message:"执行接口不可为空",trigger:"change"}
          ]
          // 'jobStatus':[
          //   {required:true,message:"任务状态不可为空",trigger:"change"}
          // ]
        }
      }
    },
    created() {
      
    },
    mounted() {
      this.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop-140
      this.getData()
      this.getData = this.debounce(this.getData)
    },
    computed:{
    },
    methods:{
      getData(num){
        if(num){
          this.formFilter.pageNum = num
        }
        this._axios("get","jobs",this.formFilter,data=>{
          this.tableData = data;
          this.total = data.length;
          if(data.length<1){
            this.$Message.warning("暂无符合条件的数据")
          }
        })
      },
      changeJobStatus(e){
        console.log(e)
        this._axios("put","jobstatus",e,data=>{
          
        })
        this.getData()
      },
      submit(){},
      showUpdate(e){
        if(e.jobStatus ==0){
          this.modalForm = e;
          this.modalTitle = "更新定时任务"
          this.submit = this.update
          this.modal = true;
        }else{
          this.$Message.error("任务状态为开启时，不可修改")
        }
        
      },
      update(){
        this.$refs.modalForm.validate(valid=>{
          if(valid){
            this._axios("put","job",this.modalForm,data=>{
              if(data){
                this.$Message.success("更新成功")
                this.modal = false;
                this.getData()
              }else{
                this.$Message.error("更新失败")
              }
            })
          }
        })
        
      },
      showInsert(){
        this.modalForm = {jobStatus:1}
        this.modalTitle = "新增定时任务"
        this.submit = this.insert
        this.modal = true
      },
      insert(){
        this.$refs.modalForm.validate(valid=>{
          if(valid){
            this._axios("post","job",this.modalForm,data=>{
              if(data){
                this.$Message.success("新增成功")
                this.modal = false;
                this.getData()
              }else{
                this.$Message.error("新增失败")
              }
            })
          }
        })
        
      },
      deleteConfirm(){
        if(!this.isSelected){
          this.$Message.error("请选择数据")
        }else{
          this.$root.mes1("warn","是否确定删除？").then(res=>{
            if(res){
              this._axios('delete',"jobs",this.$refs.table.getSelection(),data=>{
                if(data){
                  this.$Message.success("删除成功")
                  this.getData()
                  this.isSelected = false;
                }else{
                  this.$Message.error("删除失败")
                }
              })
            }
          })
        }
      }
      
    }
  }
</script>

<style lang="less">
  .home{
    position: relative;
  }
</style>



